<template>
  <div class="user-table">
    <div class="user-top">
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder="请输入内容"
            v-model="query.query"
            clearable
            @clear="clear"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="search"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="add">添加用户</el-button>
        </el-col>
      </el-row>
    </div>
    <el-table :data="userList" stripe style="width: 100%">
      <el-table-column type="index" label="#"> </el-table-column>
      <el-table-column prop="username" label="姓名"> </el-table-column>
      <el-table-column prop="email" label="邮箱"> </el-table-column>
      <el-table-column prop="mobile" label="电话"> </el-table-column>
      <el-table-column prop="role_name" label="角色"> </el-table-column>
      <el-table-column prop="mg_state" label="角色">
        <template v-slot="scope">
          <el-switch
            v-model="scope.row.mg_state"
            active-color="#13ce66"
            inactive-color="#dcdfe6"
            @change="change(scope.row)"
          >
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template v-slot="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="edit(scope.row)"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            size="mini"
            @click="delUser(scope.row.id)"
          ></el-button>
          <el-tooltip
            class="item"
            effect="dark"
            content="分配权限"
            placement="top"
          >
            <el-button
              @click="allot(scope.row)"
              type="warning"
              icon="el-icon-s-tools"
              size="mini"
            ></el-button>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      :page-sizes="[2, 3, 5, 10]"
      :page-size="query.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      @current-change="currentChange"
      @size-change="sizeChange"
    >
    </el-pagination>
    <UserDialog :ruleForm="ruleForm" :dialogVisible.sync="dialogVisible" />
    <!-- 分配权限 -->
    <el-dialog
      title="分配角色"
      :visible.sync="setRoleDialogVisible"
      width="50%"
      v-if="userInfor"
    >
      <div>
        <p>当前的用户：{{ userInfor.username }}</p>
        <p>当前的角色：{{ userInfor.role_name }}</p>
        <p>
          分配新角色：
          <el-select v-model="selectedRoleId" placeholder="请选择">
            <el-option
              v-for="item in rolesList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRoleDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="saveRoleInfo">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import UserDialog from "./UserDialog.vue";
export default {
  components: { UserDialog },
  data() {
    return {
      query: {
        query: "",
        pagenum: 1,
        pagesize: 2,
      },
      ruleForm: {
        username: "",
        password: "",
        email: "",
        mobile: "",
      },
      dialogVisible: false,
      setRoleDialogVisible: false,
      userInfor: {},
      selectedRoleId: "",
    };
  },
  created() {
    this.getUserList(this.query);
  },
  computed: {
    ...mapState({
      userListInfor: (state) => state.user.userListInfor,
      userList: (state) => state.user.userList,
      rolesList: (state) => state.user.limits,
      total: (state) => state.user.total,
    }),
  },
  methods: {
    // 更改当前页
    currentChange(value) {
      this.query.pagenum = value;
      this.getUserList(this.query);
    },
    // 更改页的展示数量
    sizeChange(value) {
      this.query.pagesize = value;
      this.getUserList(this.query);
    },
    // 更改当前状态
    change(row) {
      const obj = {
        id: row.id,
        mg_state: row.mg_state,
      };
      this.changeUserStatus(obj);
    },
    // 添加用户
    add() {
      this.dialogVisible = true;
      this.ruleForm = { username: "", password: "", email: "", mobile: "" };
    },
    // 修改用户
    edit(row) {
      this.dialogVisible = true;
      this.ruleForm = { ...row };
    },
    // 删除用户
    delUser(id) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.delUserItem(id).then(
            (res) => {
              this.$message({
                type: "success",
                message: "删除成功!",
              });
            },
            (error) => {
              this.$message({
                type: "error",
                message: error,
              });
            }
          );
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    // 获取所有角色
    allot(scope) {
      this.userInfor = scope;
      this.getLimits();
      this.setRoleDialogVisible = true;
    },
    // 保存角色更改
    saveRoleInfo() {
      this.setLimits({ id: this.userInfor.id, role: this.selectedRoleId }).then(
        (res) => {
          this.$message.success("更改成功");
          this.setRoleDialogVisible = false;
        },
        (error) => {
          this.$message.error(error);
        }
      );
    },
    // 搜索
    search() {
      this.getUserList(this.query);
    },
    // 清空按钮
    clear() {
      this.getUserList(this.query);
    },
    ...mapActions([
      "getUserList",
      "changeUserStatus",
      "delUserItem",
      "getLimits",
      "setLimits",
    ]),
  },
};
</script>

<style lang="less">
.user-table {
  .el-table {
    margin: 15px 0;
    font-size: 12px;
  }
}
</style>
