<template>
  <!-- 添加编辑用户对话框 -->
  <el-dialog
    :title="ruleForm.id ? '编辑用户' : '添加用户'"
    :visible="dialogVisible"
    width="50%"
    @close="$emit('update:dialogVisible', false)"
  >
    <el-form
      :model="ruleForm"
      ref="form"
      label-width="100px"
      :rules="addUserRules"
    >
      <el-form-item label="用户名" prop="username">
        <el-input v-model="ruleForm.username"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password" v-show="!ruleForm.id">
        <el-input v-model="ruleForm.password" show-password></el-input>
      </el-form-item>
      <el-form-item label="邮箱" prop="email">
        <el-input v-model="ruleForm.email"></el-input>
      </el-form-item>
      <el-form-item label="手机号" prop="mobile">
        <el-input v-model="ruleForm.mobile"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="$emit('update:dialogVisible', false)">取 消</el-button>
      <el-button type="primary" @click="sure">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
import { mapActions } from "vuex";
export default {
  props: ["dialogVisible", "ruleForm"],
  data() {
    return {
      addUserRules: {
        username: [{ required: true, message: "请输入账户", trigger: "blur" }],
        password: [
          { required: true, message: "请输入账户密码", trigger: "blur" },
        ],
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          { validator: this.checkEmail, trigger: "blur" },
        ],
        mobile: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          { validator: this.checkPhone, trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    checkEmail(rule, value, callback) {
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/;
      if (regEmail.test(value)) {
        return callback();
      } else {
        callback("请输入正确邮箱");
      }
    },
    //手机号验证
    checkPhone(rule, value, callback) {
      const regMobile =
        /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
      if (regMobile.test(value)) {
        return callback();
      } else {
        callback("请输入正确手机号");
      }
    },
    sure() {
      this.$refs.form.validate((valida) => {
        if (valida) {
          if (this.ruleForm.id) {
            this.editUserItem(this.ruleForm).then(
              () => {
                this.$message.success("修改成功");
              },
              (error) => {
                this.$message.error(error);
              }
            );
          } else {
            this.addUserItem(this.ruleForm).then(
              () => {
                this.$message.success("添加成功");
              },
              (error) => {
                this.$message.error(error);
              }
            );
          }
          this.$emit("update:dialogVisible", false);
        }
      });
    },
    ...mapActions(["addUserItem", "editUserItem"]),
  },
};
</script>

<style></style>
